'use client';

import { useState, useEffect } from 'react';
import { useSession } from 'next-auth/react';

export default function TestPrikbordPage() {
  const { data: session, status } = useSession();
  const [memes, setMemes] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    console.log('Test page useEffect triggered, status:', status);
    if (status === 'loading') return;
    
    loadMemes();
  }, [status]);

  const loadMemes = async () => {
    try {
      console.log('Loading memes...');
      const response = await fetch('/api/prikbord');
      const result = await response.json();
      
      console.log('API response:', result);
      
      if (result.success) {
        setMemes(result.data);
      } else {
        console.error('Error loading memes:', result.error);
      }
    } catch (error) {
      console.error('Error loading memes:', error);
    } finally {
      setIsLoading(false);
    }
  };

  if (status === 'loading' || isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-lg">Loading...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-100 p-8">
      <h1 className="text-2xl font-bold mb-4">Test Prikbord Page</h1>
      <p>Status: {status}</p>
      <p>Session: {session ? 'Logged in' : 'Not logged in'}</p>
      <p>Memes count: {memes.length}</p>
      
      <div className="mt-4">
        {memes.map((meme: any) => (
          <div key={meme.id} className="bg-white p-4 rounded shadow mb-2">
            <p>ID: {meme.id}</p>
            <p>Title: {meme.title}</p>
            <p>Image: {meme.image_url}</p>
          </div>
        ))}
      </div>
    </div>
  );
}
